body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  opacity: 0.8;
}

.board-list {
  padding-top: 65px;
}

.card-badges {
  top: 0px; 
}

ul.card-badges {
  list-style-type: none; 
}

.card-badges-right {
  right: -17px;
}

.card-badges-left {
  left: -17px;
}

.card-badges > li, .card-badges > div {
  width:26px; 
  height:26px;
}

.lane-body {
  overflow: auto;
  min-height: 182px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.card-wrapper[draggable=true]>.card, 
.lane-wrapper[draggable=true]>.lane-header {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.hover-card-badges {
  visibility: hidden;
}

.card-wrapper:hover .hover-card-badges {
  visibility: visible;
}

.card-badge {
  cursor: pointer;
}
